
import Checklist from '../../../../node_modules/vux/src/components/checklist'
import Cell from '../../../../node_modules/vux/src/components/cell'
import Popup from '../../../../node_modules/vux/src/components/popup'
import InlineDesc from '../../../../node_modules/vux/src/components/inline-desc'
import { Flexbox, FlexboxItem } from '../../../../node_modules/vux/src/components/flexbox'
import array2string from '../../../../node_modules/vux/src/filters/array2String'

import uuidMixin from '../../../../node_modules/vux/src/libs/mixin_uuid'
import TransferDom from '../../../../node_modules/vux/src/directives/transfer-dom'
import map from 'array-map'
import find from 'array-find'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'



const getObject = function (obj) {
return JSON.parse(JSON.stringify(obj))
}

export default {
directives: {
TransferDom
},
created () {
if (typeof this.show !== 'undefined') {
this.showValue = this.show
}
},
mixins: [uuidMixin],
components: {
Checklist,
Cell,
Popup,
Flexbox,
FlexboxItem,
InlineDesc,
Checker,
CheckerItem
},
filters: {
array2string,

},
props: {
valueTextAlign: {
type: String,
default: 'right'
},
title: String,
cancelText: String,
confirmText: String,
data: {
type: Array,
default () {
return []
}
},
placeholder: String,
columns: {
type: Number,
default: 0
},
fixedColumns: {
type: Number,
default: 0
},
value: {
type: Array,
default () {
return []
}
},
showName: {
type:Boolean,
default:true
},
inlineDesc: [String, Number, Array, Object, Boolean],
showCell: {
type: Boolean,
default: true
},
show: Boolean,
readonly: {
type: Boolean,
default: false
},
displayFormat: Function,
isTransferDom: {
type: Boolean,
default: true
}
},
methods: {
getName(value,list,delimiter){
if (value && !list.length) {
return ''
}
if (!delimiter) {
delimiter = ' '
}

let rs = map(value, (one, index) => {
if (list.length && Object.prototype.toString.call(list[0]) === '[object Array]') {
return find(list[index], item => {
return item.value === one
})

} else {
return find(list, item => {
return item.key === one
})
}
});

return map(rs, one => {
return one.value
})
},
getNameValues () {
return value2name(this.currentValue, this.data)
},
onClick () {
this.showValue = true
},
onHide (type) {
this.showValue = false
if (type) {
this.closeType = true
this.currentValue = getObject(this.tempValue)
}
if (!type) {
this.closeType = false
if (this.value.length > 0) {
this.tempValue = getObject(this.currentValue)
}
}
},
onPopupShow () {

if(!this.readonly){
this.showValue = true
}else{
this.showValue = false
}
},
onPopupHide (val) {
if (this.value.length > 0) {
this.tempValue = getObject(this.currentValue)
}
this.$emit('on-hide', this.closeType);
},
onPickerChange (val) {
if (JSON.stringify(this.currentValue) !== JSON.stringify(val)) {

if (this.value.length) {
const nowData = JSON.stringify(this.data)
if (nowData !== this.currentData && this.currentData !== '[]') {
this.tempValue = getObject(val)
}
this.currentData = nowData
} else { // if no value, stay quiet

}
}
this.$emit('on-shadow-change', getObject(val))
}
},
watch: {
value (val) {
if (JSON.stringify(val) !== JSON.stringify(this.tempValue)) {
this.tempValue = getObject(val)
}
},
currentValue (val) {
this.$emit('on-change', getObject(val))
this.$emit('input', getObject(val))
},
show (val) {
if(!this.readonly){
this.showValue = val
}
}
},
data () {
return {
onShowProcess: false,
tempValue: getObject(this.value),
closeType: false,
currentData: JSON.stringify(this.data), // used for detecting if it is after data change
showValue: false,
currentValue: this.value
}
}
}



