

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import Datetime from '../custom/vux-datetime'
import PopupChecker from '../custom/popup-checker/index.vue'
import PopupPicker from '../custom/popup-picker/index.vue'
import '../../../static/css/comm.css'

export default {
components: {

Group,
Cell,
XNumber,
XInput,
'popup-picker': PopupPicker,
'popup-checker': PopupChecker,
Actionsheet,
XSwitch,
XTextarea,
Datetime,
XButton,
Alert,
PopupRadio
},
data() {
return {

isPlaceholder: true,//edit 20170720  placeholderplaceholder
model: {},//
fields: [],//
isWarn: {},//
alert: {//
show: false,
content: ""
},
readonly: true,
edit_btn: true,
urlinit: {
'1': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'2': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'3': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'4': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'5': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'6': 'https://istrong.leanapp.cn/api/v1/rd/form?',
'7': 'https://istrong.leanapp.cn/api/v1/ship/shipinfo?'
},
url: '',
url_key: '1',
url_param: '',

}
},
watch: {
},
methods: {
onBlur: function (data, val) {
if (this.model[val] >= data.min && this.model[val] <= data.max) {
this.isWarn[val] = false;
} else {
this.isWarn[val] = true;
}
},
editData: function () {
this.readonly = false;
this.edit_btn = false;
},

getEditData: function () {
var that = this;
this.url_param = this.$route.query;
if (this.$route.query.urlKey) {
this.url_key = this.$route.query.urlKey;
delete this.url_param.urlKey;
}

this.isHeader = this.url_param.isHeader !== 'false';
delete this.url_param.isHeader;
this.isPlaceholder = this.url_param.isPlaceholder !== 'false';
delete this.url_param.isPlaceholder;


this.url = this.urlinit[this.url_key] + querystring.stringify(this.url_param);

this.$ajax.get(this.url).then(function (res) {
var datas = res.data.data.fields;
var objBoolean = {}
that.model = res.data.data.models;
that.fields = res.data.data.fields;
for (var i = 0, length = datas.length; i < length; i++) {

if (datas[i].control_type === "radio") {
var arr = [];
var modelName = datas[i].field;
var modelValue = that.model[modelName];
that.model[modelName] = [];//radio
that.model[modelName] = that.model[modelName].concat(modelValue);//radio


for (var j = 0, l = datas[i].data.length; j < l; j++) {
var obj = {
name: datas[i].data[j].value,
value: datas[i].data[j].key
};
arr = arr.concat(obj);
}
datas[i].data = [];//radiodata
datas[i].data[0] = arr;//radiodata
that.fields[i] = datas[i];//radiodata
}
}
for (var i = 0, len = that.fields.length; i < len; i++) {
if (that.fields[i].control_type === "number") {
objBoolean[that.fields[i].field] = false;
}
}
that.isWarn = objBoolean;





});
},

submit: function (event) {
var that = this;
var url = this.urlinit[this.url_key];
var dataModels = JSON.parse(JSON.stringify(that.model));

for (var i = 0, length = that.fields.length; i < length; i++) {

if (that.fields[i].control_type === "radio") {
var arr = [];
var modelName = that.fields[i].field;
var modelValue = dataModels[modelName];
dataModels[modelName] = "";//radio
dataModels[modelName] = dataModels[modelName].concat(modelValue);//radio

}
}
var data = {
orgId: that.$route.query.orgId,
appId: that.$route.query.appId,
trackId: that.$route.query.trackId,
projectId: that.$route.query.projectId,
issueId: that.$route.query.issueId,
models: dataModels
};
this.$ajax.post(url, data).then(function (res) {
if (res.data.status_code === 200) {











that.alert.show = true;
that.alert.content = "";
that.readonly = true;
that.edit_btn = true;

} else {
that.alert.show = true;
that.alert.content = "";
}
});
},

getParameter: function () {
if (this.$route.query.urlKey) {
this.edit_btn = false;
}
},
},

mounted: function () {

this.getParameter();
this.getEditData();


}
}



