


import Iscroll from 'iscroll'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import '../../../static/js/iconfont.js'
import '../../../static/css/comm.css'

export default {
components: {
Iscroll,
XInput,
Group,
Cell,
Checklist,
Datetime,
Radio
},
data () {
return {
models:[],//
fields:[],//
total_count:'',//
search:{





},//
sidebar:{},//
title:[],//
rdfield:[],//models
isSubShow:false,//
limit:10,//
skip:0,//
pageindex:1,//
islast:0,//
scroll: '',//
sidebar_scroll:'',//
rownum:3,//
urlinit:{
'1':'https://istrong.leanapp.cn/api/v1/rd/form?',
'2':'https://istrong.leanapp.cn/api/v1/rd/form?',
'3':'https://istrong.leanapp.cn/api/v1/rd/form?',
'4':'https://istrong.leanapp.cn/api/v1/rd/form?',
'5':'https://istrong.leanapp.cn/api/v1/rd/form?',
'6':'https://istrong.leanapp.cn/api/v1/rd/form?'
},
url:'',
url_key:'1',
url_param:'',
isLoading:false,
sidebar_height:'',
}
},
methods:{

touchMove:function(e){
document.body.scrollTop=this.sidebar_scroll;
},

theMin:function(data,val,field){
val[0]=parseFloat(val[0]);
val[1]=parseFloat(val[1]);
if(val[0]<data||(val[1]>0&&val[0]>val[1])){
this.sidebar['min'+field]=true;
}else{
this.sidebar['min'+field]=false;
}
},

theMax:function(data,val,field){
val[0]=parseFloat(val[0]);
val[1]=parseFloat(val[1]);
if(val[1]>data||(val[0]>0&&val[1]<val[0])){
this.sidebar['max'+field]=true;
}else{
this.sidebar['max'+field]=false;
}
},
clearValue:function(val,index){
this.$set(this.search[val].value,parseInt(index),'');



},

sidebarData:function(){
var that=this;
this.skip=0;
this.islast=0;
this.pageindex=1;
this.isSubShow=false;
document.body.style.overflow = 'scroll';

this.url_param=this.$route.query;
if(this.$route.query.urlKey){
this.url_key=this.$route.query.urlKey;
delete this.url_param.urlKey;
}
this.url = this.urlinit[this.url_key]+querystring.stringify(this.url_param);
for(var key in this.search){

if(this.search[key].type==="text"||this.search[key].type==="radio"){
if(this.search[key].value!==""){
this.url+='&models.'+key+'=*'+this.search[key].value+'*';
}
}else if(this.search[key].type==="number"){
if(this.search[key].value.length!==0){
this.url+='&models.'+key+'=['+this.search[key].value[0]+','+this.search[key].value[1]+']';
}
}else if(this.search[key].type==="time"){
if(this.search[key].value.length!==0){
this.url+='&models.'+key+'=['+this.search[key].value[0];
if (this.search[key].value[0].length>0) {
this.url+='*';
}
this.url+=','+this.search[key].value[1];
if (this.search[key].value[1].length>0) {
this.url+='*';
}
this.url+=']';
}
}else if(this.search[key].type==="checkbox"){
var l=this.search[key].value.length;
if(l!==0){
this.url+='&models.'+key+'=';
for(var i=0;i<l;i++){
if(i!=(l-1)){
this.url+=this.search[key].value[i]+',';
}else{
this.url+=this.search[key].value[i]+'$n';
}
}
}
}
}
this.$ajax.get(that.url+'&$limit='+this.limit+'&$skip='+this.skip).then(function(res){
var obj={};
that.models = res.data.data.models;
that.fields=res.data.data.fields;
that.total_count=res.data.data.total_count;

















if(res.data.data.models.length===that.limit){
that.skip = that.limit*(that.pageindex++);
}else{
that.islast = 1;
}

if(that.fields){
for(var i=0,len=that.fields.length;i<len;i++){
if(that.fields[i].show_list){
for (var j = 0; j < (that.rownum+1); j++) {
if (that.fields[i].show_list.index===j) {
that.title[j] = that.fields[i].title;
that.rdfield[j] = that.fields[i].field;
break;
}
}
}
}
}

if(that.title.length===0){
for(var i=0;i<(that.rownum+1);i++){
that.title[i] = that.fields[i].title;
that.rdfield[i] = that.fields[i].field;
}
}

});
},

showDetail:function(item){
var issueId =[];
window.location.href='./#/home/add?'+querystring.stringify(this.$route.query)+"&issueId="+item.issueId;

},

showScreen:function(){
this.isSubShow = !this.isSubShow;
if(this.isSubShow===true){
document.body.style.overflow = 'hidden';
}else{
document.body.style.overflow = 'scroll';
}
this.sidebar_scroll=document.body.scrollTop;
},

menu:function(e){
var that = this;
this.scroll = document.body.scrollTop;
var winheight = window.screen.height;
var bodyheight = document.body.clientHeight;
if(this.isSubShow===true){
document.body.scrollTop=this.sidebar_scroll;
e.preventDefault();
return false;
}
if(that.islast===0){
if((this.scroll+winheight)>(bodyheight-20)){

if(!that.isLoading ){
that.isLoading = true;
that.$ajax.get(that.url+'&$limit='+that.limit+'&$skip='+that.skip).then(function(res){
that.models = that.models.concat(res.data.data.models);

that.fields =res.data.data.fields;

if(res.data.data.models.length===that.limit){
that.skip = that.limit*(that.pageindex++);


}else{
that.islast = 1;
}
that.isLoading=false;




});
}
}
}
},
sidebarHeight: function(){
var that=this;
var bodyheight;


var timer=setInterval(function(){
if(window.innerHeight>50){
bodyheight=window.innerHeight;
that.sidebar_height=(bodyheight-41-53)+'px';
clearInterval(timer);
}
},1000);
},








stopScrolling:function ( touchEvent ) {

























}
},

mounted:function(){
this.sidebarHeight();
var that = this;
window.addEventListener('scroll', this.menu);//











this.url_param=this.$route.query;
if(this.$route.query.urlKey){
this.url_key=this.$route.query.urlKey;
delete this.url_param.urlKey;
}
this.url = this.urlinit[this.url_key]+querystring.stringify(this.url_param);
this.$ajax.get(that.url+'&$limit='+this.limit+'&$skip='+this.skip).then(function(res){
var obj={};
var objBoolean={}
that.models = res.data.data.models;
that.fields=res.data.data.fields;
that.total_count=res.data.data.total_count;
for (var i = 0,len=that.fields.length; i <len; i++) {
if(that.fields[i].search===true){
if(that.fields[i].control_type==="text"||that.fields[i].control_type==="radio"){
obj[that.fields[i].field]={
type:that.fields[i].control_type,
value:""
}
}else if(that.fields[i].control_type==="number"||that.fields[i].control_type==="checkbox"||that.fields[i].control_type==="time"){
obj[that.fields[i].field]={
type:that.fields[i].control_type,
value:[]
}
}
if(that.fields[i].control_type==="number"){
objBoolean['min'+that.fields[i].field]=false;
objBoolean['max'+that.fields[i].field]=false;
}else if(that.fields[i].control_type==="radio"||that.fields[i].control_type==="checkbox"||that.fields[i].control_type==="time"){
objBoolean[that.fields[i].field]=false;
}
}
}
that.search=obj;
that.sidebar=objBoolean;

if(res.data.data.models.length===that.limit){
that.skip = that.limit*(that.pageindex++);

}else{
that.islast = 1;
}

if(that.fields){
for(var i=0,len=that.fields.length;i<len;i++){
if(that.fields[i].show_list){
for (var j = 0; j < (that.rownum+1); j++) {
if (that.fields[i].show_list.index===j) {
that.title[j] = that.fields[i].title;
that.rdfield[j] = that.fields[i].field;
break;
}
}
}
}
}

if(that.title.length===0){
for(var i=0;i<(that.rownum+1);i++){
that.title[i] = that.fields[i].title;
that.rdfield[i] = that.fields[i].field;
}
}
});
}
}



